"use client"

import * as React from "react"
import * as ProgressPrimitive from "@radix-ui/react-progress"

import { cn } from "@/packages/neu/src/lib/utils"

const Progress = React.forwardRef<
    React.ElementRef<typeof ProgressPrimitive.Root>,
    React.ComponentPropsWithoutRef<typeof ProgressPrimitive.Root>
>(({ className, value, ...props }, ref) => (
    <ProgressPrimitive.Root
        ref={ref}
        className={cn(
            "bg-neumorphic-bg border border-neumorphic-border shadow-neumorphic-inset",
            "relative h-3 w-full overflow-hidden rounded-full transition-all duration-200",
            className,
        )}
        {...props}
    >
      <ProgressPrimitive.Indicator
          className="h-full w-full flex-1 bg-gradient-to-r from-neumorphic-primary to-neumorphic-primary-light shadow-neumorphic-flat transition-all duration-500 ease-out"
          style={{ transform: `translateX(-${100 - (value || 0)}%)` }}
      />
    </ProgressPrimitive.Root>
))

export { Progress }
